body,
html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(48, 48, 48);
}

 #map {
   height: 100vh;
   width: 100%;
 }

 h1 {
   text-align: center;
   font-size: 1.2em;
   margin:1.4em 3.4em;
   color: #fff;
 }

 #menu {
  display: inline-block;
  position: absolute;
  top:0em;
  right: .4em;
  font-size: 3em;
  fill: #E0E0E0;
  z-index: 10;
  color: #fff;
  cursor: pointer;
}

.nav {
  z-index: 10;
  background-color: #fff;
  width: 0;
  position: absolute;
  right:0;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  margin-left: auto;
  margin-right: auto;
  overflow: hidden;
}

#search, .showing-locations {
  display: none;
}

.locations-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.location-item {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: inline-block;
  display: list-item;
  border-bottom: 1px solid #E0E0E0;
  line-height: 24px;
  width: 0;
  padding:0;
  text-align: left;
  cursor: pointer;
}

.nav.open {
  width: 300px;
  border: 1px solid #E0E0E0;
  overflow: scroll;
  overflow-x: hidden;
  height: 100vh;
}

.nav.open .location-item {
  width:100%;
  padding: 0.5em 1.6em;
}

.nav.open #search, .nav.open .showing-locations {
  display: block;
  margin: 0.5em 1.6em;
  width: 200px;
}

.nav.open #search {
  border: 1px solid rgb(177, 177, 177);
}

.location-name {
  font-size: 1.1em;
}

.location-type {
  font-size: 0.9em;
  color: rgb(136, 136, 136);
}

.message-cont {
  padding-top: 10vh;
  margin: auto;
  max-width: 500px;
  text-align: center;
}

.generic-message {
  font-size: 2em;
  color: rgb(136, 136, 136);
}

.contact-dev-message {
  font-size: 1.2em;
  color: rgb(136, 136, 136);
}

.specific-message {
  padding-top: 3em;
}

.foursquare {
  margin: 0.7em 0 0 1.6em;
  width: 300px;
  height: 20px;
  background: url(img/powered-by-foursquare-grey.png);
  background-size: 180px;
  background-repeat: no-repeat;
}

@media screen and (max-width: 650px) {
  .nav.open {
    width: 200px;
  }
  .nav.open .location-item {
    padding: 0.5em 0 0.5em 1em;
  }
  .nav.open #search, .nav.open .showing-locations {
    margin: 0.5em 0 0.5em 0.8em;
    width: 180px;
  }
  .foursquare {
    margin: 0.7em 0 0 1em;
    width: 200px;
    background-size: 160px;
  }
}


